<template>
	<div id="pintuangoods-post">
		<public-header title="添加拼团商品"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell>
					<div class="upload-img">
						<div class="flex-lr goods-img">
							<div>商品图片</div>
							<div class="c-gray">最多5张,第一张为商品列表页图片</div>
						</div>
						<uploader @onGetUrl="onGetImgsUrl" :max="5"></uploader>
					</div>
				</van-cell>
				<van-field
						v-model="records.name"
						label="商品名称"
						placeholder="请填写商品名称"
						input-align="right"
				/>
				<van-field
						v-model="records.total"
						label="商品库存"
						placeholder="请填写商品库存"
						input-align="right"
				/>
				<van-field
						v-model="records.unit"
						label="商品单位"
						placeholder="请填写商品单位"
						input-align="right"
				/>
				<van-cell title="商品详情">
					<div slot="right-icon" class="flex">
						<span>未编辑</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="拼团分类">
					<div slot="right-icon" class="flex" @click="onTogglePopup">
						<span>{{records.category_title ? records.category_title: '请选择'}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-field
						v-model="records.price"
						label="团购价"
						type="number"
						placeholder="请输入团购价"
						input-align="right"
				/>
				<van-field
						v-model="records.aloneprice"
						label="单购价"
						type="number"
						placeholder="请输入团购价"
						input-align="right"
				/>
				<van-field
						v-model="records.oldprice"
						label="市场价"
						type="number"
						placeholder="请输入市场价"
						input-align="right"
				/>
				<van-cell>
					<div slot="title">定时发售</div>
					<iswitch :value="records.islimittime" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="records.islimittime"></iswitch>
				</van-cell>
				<template v-if="records.islimittime == 1">
					<van-cell title="活动开始时间">
						<div slot="right-icon" class="flex" @click="onToggleDatePicker('start')">
							<span>{{this.records.starttime_cn ? this.records.starttime_cn: '点击选择'}}</span>
							<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell title="活动结束时间">
						<div slot="right-icon" class="flex" @click="onToggleDatePicker('end')">
							<span>{{this.records.endtime_cn ? this.records.endtime_cn: '点击选择'}}</span>
							<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
				</template>
				<van-cell>
					<div slot="title">活动状态</div>
					<iswitch :value="records.status" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="records.status"></iswitch>
				</van-cell>
			</van-cell-group>
			<van-radio-group class="margin-10-t" v-model="records.usetype">
				<van-cell-group>
					<van-cell title="消费方式"></van-cell>
					<van-cell title="到店消费" clickable @click="records.usetype = '1'" class="border-0px">
						<van-radio name="1" />
					</van-cell>
					<van-cell title="快递上门" clickable @click="records.usetype = '2'" class="border-0px">
						<van-radio name="2" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">高级设置</div>
					<iswitch :value="bestsetting" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="bestsetting"></iswitch>
				</van-cell>
			</van-cell-group>
			<template v-if="bestsetting == '1'">
				<van-cell-group class="margin-10-t">
					<van-field
						v-model="records.peoplenum"
						label="开团人数"
						placeholder="请输入开团人数"
						input-align="right"
					/>
					<van-field
						v-model="records.grouptime"
						label="组团时间"
						placeholder="单位小时"
						input-align="right"
					/>
					<van-field
						v-model="records.falesailed"
						label="虚拟销量"
						placeholder="请输入虚拟销量"
						input-align="right"
					/>
					<van-field
						v-model="records.buylimit"
						label="单次限购"
						placeholder="请输入单次限购"
						input-align="right"
					/>
				</van-cell-group>
			</template>
			<div class="padding-15">
				<van-button size="normal" block class="bg-info" @click="onSubmit">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<!--商品分类picker-->
		<van-popup v-model="goodsCategory" position="bottom">
			<van-picker
					show-toolbar
					title="所属分类"
					:columns="category"
					value-key="title"
					@cancel="onTogglePopup"
					@confirm="onConfirmCategory"
			/>
		</van-popup>
		<!--时间picker-->
		<van-popup class="popup-time" v-model="datePicker.status" position="bottom">
			<van-datetime-picker
					v-model="datePicker.currentDate"
					type="datetime"
					:min-data="datePicker.currentDate"
					:max-date="datePicker.maxDate"
					@confirm="onConfirmTime"
					@cancel="onToggleDatePicker"
			/>
		</van-popup>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	import Uploader from '@/components/uploader'
	export default {
		data() {
			return {
				id: 17,
				goodsCategory: false,
				category: [],
				datePicker: {
					type: '',
					maxDate: new Date(2050, 12),
					currentDate: new Date(),
					status: false,
				},
				bestsetting: '0',
				showPreLoading: true,
				records: {
					name: '',
					total: '',
					unit: '',
					cateid: '',
					category_title: '',
					price: '',
					aloneprice: '',
					oldprice: '',
					islimittime: 0,
					starttime_cn: '',
					endtime_cn: '',
					status: 1,
					usetype: '1',
					peoplenum: '',
					grouptime: '',
					falesailed: '',
					buylimit: '',
				},
			}
		},
		components: {
			publicHeader,
			iswitch,
			Uploader
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: that,
					url: 'manage/pintuan/goods/post',
					data: {
						id: that.id
					},
					success: (res) => {
						that.records = that.util.extend(that.records, res.records);
						if(res.category){
							that.category = [...that.category, ...res.category];
						}
					}
				});
			},
			onGetImgsUrl(data){

			},
			onTogglePopup(){
				this.goodsCategory = !this.goodsCategory;
			},
			onConfirmCategory(data){
				this.records.cateid = data.id;
				this.records.category_title = data.title;
				this.onTogglePopup();
			},
			onToggleDatePicker(type) {
				this.datePicker.status = !this.datePicker.status;
				if(type){
					this.datePicker.type = type;
				}
			},
			onConfirmTime(value){
				var time = this.util.formatDate(value);
				if(this.datePicker.type == 'start'){
					this.records.starttime_cn = time;
				} else {
					this.records.endtime_cn = time;
				}
				this.onToggleDatePicker();
			},
			jsToggleSwitch(data){
				this.util.jsToggleSwitch({
					vue: this,
					key: data.keys,
					value: data.value
				});
			},
			onSubmit(){
				var that = this;
				setData({
					vue: that,
					url: 'manage/pintuan/goods/post',
					data: {
						id: that.id,
						data: that.records
					},
					redirect: that.util.getUrl({path: '/pages/pintuan/goods/list'}),
				});
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>

</style>


